import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
    state:{
        products:[
            {name:"Herry",price:200},
            {name:"Bukcy",price:150},
            {name:"Elicy",price:100},
            {name:"joket",price:50},
          ]
    },
    getters:{
        saleProducts:(state) =>{
            var saleProducts = state.products.map(product =>{
                return {
                    name:"**" + product.name +"**",
                    price:product.price/2
                }
            });
            return saleProducts;
        }
    },
    mutations:{
        reduce1:(state,payload) => {
            state.products.forEach(product=>{
                product.price -= payload;
            })
        }
    },
    actions:{
        //这里的context相当于this.$store /payload是接收传过来的参数(amount)
        reduce2:(context,payload)=>{
            setTimeout(function(){
               context.commit('reduce1',payload);
               //将payload传给reduce1
            },2000);
        }
    }
});


