<template>
    <div id="two">
        <h1>productlisttwo</h1>
        <ul v-for="product in saleProducts">
            <li>
                <span>{{product.name}}</span>
                <span>${{product.price}}</span>
            </li>
        </ul>
    </div>
</template>
<script>
export default{
    computed:{
        products(){
            return this.$store.state.products;
        },
        saleProducts(){
            return this.$store.getters.saleProducts;
        }
    }
    // props:["products"],
    // data(){
    //     return {
            
    //     }
    // }
}    
</script>
<style scoped>
#two{
    padding: 10px 10px;
    background:skyblue;
    margin-bottom: 30px;
}
ul{
    padding: 0; 
}
li{
    margin-top:10px;
    padding: 20px 10px;
    background: rgb(255,255,255,0.7);
}
</style>



