<template>
    <div id="one">
    <h1>productlistone</h1>
    <ul v-for="product in saleProducts">
        <li>
           <span>{{product.name}}</span>
           <span class="price">${{product.price}}</span>
        </li>
    </ul>
    <button @click="reducePrice(5)">商品降价</button>
    </div>
</template>
<script>
import {mapGetters} from 'vuex'
import {mapActions} from 'vuex'
//这是新的es6 vuex的方法，需要安装bebel来识别 npm install babel-preset-stage-2 --save-dev

export default{
   computed:{
       products(){
           return this.$store.state.products;
           //调用state
       },
    //    saleProducts(){
    //        return this.$store.getters.saleProducts;
    //        //调用getters
    //    },

       //调用geeters的新方法
       ...mapGetters([
           "saleProducts",
           "function2"
       ])
   },
   methods:{
       ...mapActions({
           reducePrice: "reduce2",
           //将 `this.reducePrice()` 映射为 `this.$store.dispatch('reduce1')`
       })
        // reducePrice(amount){
           //这样调用vuex在严格模式下回调用，
        //   this.$store.state.products.forEach(product =>{
        //       product.price -= 2;
        //   })

        // this.$store.commit('reduce1');
        //commit里面是调用vuex的mutation里的方法，在严格模式下正常使用

        // this.$store.dispatch('reduce2',amount);
        //dispatch(分发)是调用action的方式
        //上面的5传给amount，通过这将amount传给Vuex actions 的方法的payload
        //},
   }
    // props:["products"],
    // data(){
    //     return {

    //     }
    // }
}    
</script>
<style scoped>
#one{
    padding: 10px 20px;
    background:#D1E4FF;
    margin-bottom: 30px;
    height: 200px;
}
ul{
    padding: 0; 
    list-style: none;
}
li{
    float: left;
    margin-right:20px;
    margin-top:10px;
    padding: 20px 10px;
    background: rgb(255,255,255,0.7);
}
.price{
    font-weight: bold;
}
</style>



